body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
font-family: 'Montserrat', sans-serif;
overflow-x: hidden;


}
html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    position: relative;
}
li, ol {
  list-style: none; }

a:link, a:visited, a:hover, a:active {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
  outline: none;
}

input, button, select, option {
  background-clip: padding-box;
  border: none; }

input:focus, textarea:focus, select:focus, button:focus {
  outline: 0; }

p{
    line-height: 1.6;
    margin: 0 0 2rem;
}

body {
  font-size: 1.5rem;
  text-align: left;
  color: #a6a7aa; }

.wrapper {
  width: 110rem;
  margin: auto; }

.cover {
  -webkit-align-content: center;
          align-content: center;
  display: grid;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%; }

.btn {
  display: inline-block;
  padding: 1.2rem 0;
  width: 24rem;
  text-align: center;
  border-radius: 2.5rem;
  font-size: 1.6rem;
  margin: 1rem;
  cursor: pointer; }

section {
  background: #fff;
  width: 100%;
  text-align: left;
  display: grid;
  -webkit-align-content: center;
          align-content: center; }
  section .wrapper {
    display: grid;
    grid-template-columns: 35rem 1fr;
    padding: 17rem 0 12rem; }

.section__title {
  font-size: 2.6rem;
  font-weight: bold;
  text-align: right;
  color: #515769;
  padding-right: 10rem; }

